@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&family=Nanum+Gothic:wght@400;700;800&family=Work+Sans&display=swap');

:root {
  --primaryColorValue: 206;
  --primaryRegularColor: hsla(var(--primaryColorValue), 69%, 62%, 1);
  --primaryRegularColorLight: hsla(var(--primaryColorValue), 62%, 54%, 1);
  --blackTextColorValue: 7%;
  --blackTextColor: hsla(0, 0%, var(--blackTextColorValue), 1);
}

body {
  font-family: 'Nanum Gothic', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--blackTextColor);
}

*, p, input, h1, h2, h3, h4 {
  margin: 0;
  padding: 0;
  outline: none;

  &::selection {
    background: black;
  }

  &::-moz-selection {
    background: black;
  }
}

h1, h2, h3, h4 {
  font-family: Montserrat, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

// ANTD STYLES
// ant-menu
.ant-menu-submenu-title, .ant-menu-item {
  color: var(--blackTextColor);

  &:hover {
    color: var(--primaryRegularColor) !important;

  }

  & i:before, & i:after {
    background-image: linear-gradient(90deg, var(--primaryRegularColor), var(--primaryRegularColor)) !important;
  }
}

.ant-menu-submenu-active, .ant-menu-item-active {
  color: var(--primaryRegularColor) !important;
}

.ant-menu-horizontal .ant-menu-item-selected {
  color: var(--primaryRegularColor);
}

.ant-menu-horizontal .ant-menu-item a {
  color: var(--blackTextColor);
}

.ant-menu-item a {
  color: var(--blackTextColor);

  &:hover {
    color: var(--primaryRegularColor);
  }
}

// ant-input
.ant-input-affix-wrapper-focused {
  border-color: var(--primaryRegularColor) !important;
}

// ant-checkbox
.ant-checkbox-checked .ant-checkbox-inner {
  background-color: var(--primaryRegularColor);
  border-color: var(--primaryRegularColor);
}

.ant-checkbox:hover .ant-checkbox-inner, .ant-checkbox-checked:after {
  border-color: var(--primaryRegularColor);
}

.ant-checkbox-input:focus + .ant-checkbox-inner {
  border-color: var(--primaryRegularColor);
}
