.home__service-categories {
  max-width: 1190px;
  margin: 160px auto;
  padding: 0 10px;
}

.home__service-categories__title {
  display: flex;

  img {
    width: 100%;
    height: 376px;
  }
}

.home__service-categories__title__info {
  min-width: 360px;
  max-width: 360px;

  span {
    color: var(--primaryRegularColor);
    font-size: 15px;
    font-weight: 500;
    line-height: 18px;
  }

  p {
    font-size: 15px;
    line-height: 24px;
    opacity: 0.5;
    margin-top: 30px;
  }
}

.home__service-categories__list__item {
  display: flex;
  align-items: center;
  margin-bottom: 30px;

  h2 {
    width: 50%;
    text-align: center;
    font-size: 30px;
    font-weight: 400;
    line-height: 49px;
  }
}

.home__service-categories__list__item__info {
  width: 50%;
  box-shadow: rgba(0, 0, 0, 0.1) 0 20px 50px -20px;
  font-size: 15px;
  line-height: 24px;
  margin: 0 93px;
  background-color: rgba(91, 167, 225, 0.1);
  padding: 30px 40px 35px;

  p {
    font-size: 18px;
    line-height: 30px;
  }
}

.home__service-categories__img {
  width: 460px !important;
  height: 300px !important;
  //border-radius: 300px;
  margin-left: 230px;
  margin-bottom: 40px;
}

@media screen and (max-width: 800px) {
  .home__service-categories__img {
    width: 40% !important;
    height: 40% !important;
    margin-left: 180px;
  }

  .home__service-categories__title {
    flex-direction: column;
  }

  .home__service-categories__title__info {
    margin: 0 auto;
  }

  .home__service-categories__list__item {
    flex-direction: column;

    h2,
    .home__service-categories__list__item__info {
      width: 70%;
    }
  }
}

@media screen and (max-width: 500px) {
  .home__service-categories__img {
    width: 60% !important;
    height: 60% !important;
    margin-left: 80px;
  }

  .home__service-categories {
    margin-top: 60px;
  }

  .home__service-categories__title__info {
    min-width: auto;
    text-align: center;
  }

  .home__service-categories__list__item {
    h2,
    .home__service-categories__list__item__info {
      width: 90%;
      margin: 0 auto !important;
    }

    h2 {
      font-size: 25px;
    }

    p {
      font-size: 16px;
    }
  }
}

.footer-clm-flex {
  display: flex;
  flex-direction: row;
}

@media (max-width: 850px) {
  .footer__top__item h2:after {
    width: auto !important;
  }
  .articles-link{
    margin-left: 0 !important;
  }
  .footer__top__item {
    width: 50%;
    display: flex;
    // align-items: center;
    align-items: flex-start;
    margin-left: 0px;
  }
  .footer__top {
    padding: 0 !important;
  }

  .home__service-categories {
    margin-bottom: 50px !important;
  }
  .hide-block {
    display: none !important;
  }

  .show-block {
    margin: 0 auto;
    width: auto !important;
    display: flex !important;
  }
}
@media (min-width: 851px) {
  .footer__top__item h2 {
    display: inline !important;
  }
  .footer-clm-flex {
    flex-grow: 1;
    justify-content: space-evenly;
  }

  .hide-block {
    display: flex !important;
  }
  .show-block {
    display: none !important;
  }
}
