.financing__title {
  position: relative;
  padding: 0 40px;
  margin-top: 80px;
}

.financing__title__bg {
  background-image: url("../../../src/img/financing.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  filter: grayscale(100%);
  width: 100%;
  height: 300px;
}

.financing__title__content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 50px;
  text-align: center;


  h1 {
    color: white;
    font-size: 50px;
    font-weight: 700;
    text-shadow: 1px 1px 1px black;
    line-height: 1;
    margin-bottom: 20px;
  }
}

.financing__title__content__links {
  display: flex;
  color: white;
  font-size: 15px;
  text-shadow: 1px 1px 1px black;

  a {
    color: white;

    &:hover {
      color: var(--primaryRegularColor);
    }
  }

  span {
    margin: 0 10px;
  }
}

.financing__content {
  font-size: 15px;
  line-height: 200%;
  max-width: 860px;
  margin: 80px auto 0;
  padding: 0 10px;

  h2 {
    font-size: 30px;
    font-weight: 700;
  }

  ul {
    list-style-type: inherit !important;
    opacity: 0.5;
    padding-left: 20px;
  }
}

.financing__content__collapse {
  .ant-collapse-item {
    border-bottom: 1px solid hsla(0, 0%, 0%, 0.1);
  }

  .ant-collapse-header {
    font-weight: 700;

    span {
      color: var(--primaryRegularColor) !important;
    }
  }

  .ant-collapse-content-box {
    color: var(--blackTextColor)
  }
}

.financing__content__services__wrapper {
  margin-top: 40px;
  display: flex;
}

.financing__content__services {
  width: 50%;
  margin-right: 20px;

  h2 {
    font-size: 20px;
    font-weight: 500;
    line-height: 2;
  }

  ul {
    list-style-type: none !important;
    opacity: 1;
    margin-top: 20px;

    span {
      color: var(--primaryRegularColor);
    }
  }
}

.financing__content__offering {
  margin-top: 40px;
  border-bottom: 1px solid hsla(0, 0%, 0%, 0.1);
  margin-bottom: 100px;
  padding-bottom: 60px;

  h2 {
    font-size: 20px;
    font-weight: 500;
    line-height: 2;
  }
}

.financing__content__services__img {
  background-image: url("../../../src/img/finance_services.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  height: 270px;
  width: 50%;
  border-radius: 10px;
}

.financing__content__offering__chart {
  width: 600px !important;
  height: 300px !important;
  margin-left: -63px;
  margin-top: 30px;
}

#chartMobile {
  display: none !important;
}

@media screen and (max-width: 870px) {
  .financing__content__offering__chart {
    margin: 30px auto 0;
  }
}

@media screen and (max-width: 760px) {
  .financing__content__services__wrapper {
    flex-direction: column-reverse;
  }

  .financing__content__services__img {
    width: 100%;
  }

  .financing__content__services {
    margin-right: 0;
    margin-top: 20px;
    width: 100%;
  }
}

@media screen and (max-width: 630px) {
  #chartWithLegend {
    display: none !important;
  }

  #chartMobile {
    display: block !important;
  }

  .financing__content__offering__chart {
    width: 100% !important;
  }
}

@media screen and (max-width: 500px) {
  .financing__title {
    padding: 0;
    margin-top: 0;
  }
  .financing__title__content h1 {
    font-size: 40px;
  }
}