.home__services {
  max-width: 1190px;
  margin: 160px auto 0;
  padding: 0 10px;
}

.home__services__title {
  display: flex;
  justify-content: space-between;

  h2 {
    max-width: 525px;
  }
}

@media screen and (max-width: 500px) {
  .home__services {
    margin-top: 60px;
  }

  .home__services__title {
    flex-direction: column;
    text-align: center;

    h2 {
      font-size: 40px;

      &:after {
        margin: 10px auto 0;
        width: 200px;
      }
    }

    a {
      margin: 30px auto;
    }
  }
}