.privacy_flex-column {
  display: flex;
  flex-direction: column;
  margin-top: 50px;
  margin-bottom: 50px;
}

.privacy-warning-img {
  width: 190px;
  height: 170px;
  margin: 0 auto;
}

.privacy_text-ol {
  padding: 10px 40px;
  a {
    cursor: pointer;
  }
}

.privacy_text-ol {
  li {
    padding: 5px 0;
  }
}

.privacy-block {
  p {
    font-size: 14px !important;
  }
  h4 {
    font-size: 18px;
    margin-top: 20px;
  }
  i {
    margin-top: 10px !important;
  }
}
