.home__end-question {
  margin: 160px auto;
  font-size: 30px;
  font-family: Montserrat, sans-serif;
  font-weight: 500;
  line-height: 24px;
  padding: 0 10px;
  text-align: center;
  opacity: 0.7;

  &:after {
    content: "";
    display: block;
    height: 3px;
    width: 100px;
    background-color: grey;
    opacity: 0.1;
    margin: 20px auto 0;
  }
}

@media screen and (max-width: 500px) {
  .home__end-question {
    margin: 60px auto;
    font-size: 26px;
  }
}