.callback-form__wrapper {
  min-width: 255px;
  background-image: url("../../../src/img/call.png");
  background-repeat: no-repeat;

  background-size: 40%;
  padding-top: 405px;
  margin: -100px auto 0;
  display: flex;
  justify-content: center;
  align-content: center;
  background-position: top;
}

.callback-form {
  box-shadow: 0 20px 20px -20px rgba(0, 0, 0, 0.10);
  //padding: 27px 15px;
  position: relative;
  width: 60%;
  margin: 0 auto 60px auto;

  padding: 20px;

  background: #f7f7f7;

  h3 {
    font-family: Montserrat, sans-serif;
    font-size: 18px;
    font-weight: 500;
    line-height: 19px;
    margin-bottom: 20px;
  }

  input {
    background-color: white;
    font-size: 12px;
    border: none;
    padding: 10px;
    border-radius: 4px;
    margin-bottom: 10px;
    width: 100%;
  }

  a {
    text-align: center;
    width: 200px;
    margin: 0 auto;
  }
}

@media screen and (max-width: 800px) {
  .callback-form__wrapper {
    background-size: 100%;
    padding-top: 200px;
  }

  .callback-form {
    a {
      width: 150px;
      margin: 20px auto;
    }
  }
}
