.privacy_flex-column {
  display: flex;
  flex-direction: column;
  margin-top: 50px;
  margin-bottom: 50px;
}

.blog_flex-column {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  margin-bottom: 20px;
}

.privacy-warning-img {
  width: 190px;
  height: 170px;
  margin: 0 auto;
}

.privacy-warning-img-page {
  width: 50%;
  //height: 270px;
  margin: 0 auto;
}

.article-head-img {
  width: 100%;
  margin: 0 auto;
}

.privacy-warning-img-article {
  width: 100%;
  //height: 270px;
  margin: 0 auto;
}

.privacy_text-ol {
  padding: 10px 40px;
}

.privacy_text-ol {
  li {
    padding: 5px 0;
  }
}

.article-p-small {
  font-size: 11px;
  margin: 5px 0;
}

.article-p-margin {
  margin: 10px 0;
}

.flex-block {
  display: flex;
  width: 40%;
  justify-content: center !important;

  margin: 0 auto;
  .left-flex {
    width: 60% !important;
  }
  .right-flex {
    width: 50% !important;
  }

}

.leftimg {
  float: right !important;
  height: 400px;
  //min-width: 400px !important;
  margin-right: 40px;
  //margin: 5px !important;
}

.rightFloat {
  float: left !important;
  height: 400px;
  margin-left: 40px;
  //margin: 5px !important;
}



.leftimg-small {
  float: right !important;
  height: 300px;
  min-width: 400px !important;
  margin-right: 40px;
}

.rightImg-small {
  height: 300px;
  min-width: 400px !important;
  margin-left: 50px !important;
  float: left !important;
}

.leftimg-sml {
  float: right !important;
  height: 290px;
  min-width: 400px !important;
  margin-right: 20px;
}

.rightImg-sml {
  height: 290px;
  width: 400px;
  margin-left: 40px !important;
  float: left !important;
}

.rightImg-mini {

  float: left !important;
  height: 210px ;
  //margin-top: 40px;
  min-width: 480px;
  margin-left: 90px;

}
.marginTop100 {
  margin-top: 100px !important;
}

.marginTop80 {
  margin-top: 80px !important;
}

.marginTop60 {
  margin-top: 60px !important;
}

.marginTop50 {
  margin-top: 50px !important;
}

.marginTop40 {
  margin-top: 40px !important;
}

.marginTop30 {
  margin-top: 30px !important;
}

.marginTop20 {
  margin-top: 20px !important;
}

.marginTop10 {
  margin-top: 10px !important;
}

.column-wrap {
  padding: 0 !important;
}



@media screen and (max-width: 500px) {
  .blog_flex-mobile {
    flex-direction: column !important;
    margin: 50px 0 !important;
  }
  .rightImg-small {
    margin-left: 0 !important;
    width: 90% !important;
    min-width: 350px !important;
  }
  .rightFloat, .rightImg-mini {
    margin-left: 0 !important;
    width: 90% !important;
    min-width: 350px !important;
    height: 250px;
    margin-bottom: 10px;
  }
  .rightImg-sml {
    margin-left: 0 !important;
    width: 90% !important;
    height: 240px;
    min-width: 350px !important;
  }
  .leftimg-small {
    margin-left: 0 !important;
    width: 90% !important;
    min-width: 350px !important;
    margin-bottom: 10px;
  }

  .leftimg-sml {
    margin-left: 0 !important;
    width: 90% !important;
    min-width: 350px !important;
  }
  .leftimg {
    height: 240px !important;
    margin-left: 0 !important;
    width: 90% !important;
    min-width: 350px !important;
    margin-bottom: 10px;
  }

}

