.home__greeting__wrapper {
  position: relative;
}

.home__greeting {
  background-image: url('../../../../img/01.jpg');
  background-size: cover;
  //background-position: 50% 50%;
  height: 814px;
  top: -10px;
  filter: grayscale(1) contrast(1.1) brightness(1.1);
  position: relative;
}


.home__greeting__message {
  //transform: skewX(-10deg);
  background-color: white;
  //width: max-content;
  position: absolute;
  min-width: 600px;
  width: calc(100% - 80px);
  //right: 64%;
  top: 100px;
  margin: 0 40px;
  padding: 20px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: rgba(0, 0, 0, 0.1) 0 20px 50px -20px;
  //border-radius: 20px;


  h1 {
    line-height: 70px;
    font-weight: 700;
    font-size: 50px;
    color: var(--blackTextColor);
    text-decoration: none;
    white-space: normal;

    span {
      color: var(--primaryRegularColor);
    }
  }

  p {
    font-family: "Nanum Gothic", sans-serif;
    line-height: 28px;
    font-weight: 400;
    font-size: 18px;
    opacity: 0.7;
  }

  b {
    line-height: 25px;
    font-weight: 700;
    font-size: 18px;
  }
}

.home__greeting__btn {
  //position: absolute;
  display: block;
  margin: 20px 0;
  left: 10px;
  //bottom: 10px;
  font-weight: 700;

  &:hover {
    background-color: white;
    color: var(--blackTextColor);
    box-shadow: none !important;
  }
}

@media screen and (max-width: 1040px) {
  .home__greeting__message {
    left: 0;
    right: auto;
  }
}

@media screen and (max-width: 500px) {
  .home__greeting__message {
    width: 100%;
    text-align: center;

    h1 {
      font-size: 40px;
    }
  }

  .home__greeting__btn {
    left: 50%;
    transform: translate(-50%);
  }
}
