.header__wrapper {
  padding: 0 40px 20px;
  border-bottom: 1px solid #f2f2f2;
  z-index: 2;
  position: fixed;
  width: 100%;
  background-color: white;
}

//search

.search_container {
  display: flex;
  flex-direction: column !important;
  background-color: red !important;

}

.search_result {
  position: absolute;
  top: 120px;
  right: 0;
  width: 200px;
  background-color: white;
}

.search_result_line {
  padding: 5px 0;
}

// header__contacts styles
.ant-menu {
  margin-top: 35px !important;
}

.header__menu {
  /* margin-top: 20px; */
  margin-top: -25px;
  align-items: center;
}

.ant-menu {
  margin-top: 35px;
}

.main-wrapper__barrier {
  padding-bottom: 107px;
}


.header__menu__list__wrapper > div:nth-child(2) {
  margin-top: 35px;
}


span.ant-input-search:nth-child(2) {
  margin-top: 10px;
}


.header__contacts,
.header__contacts__regular,
.header__contacts__social,
.header__contacts__regular__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: var(--blackTextColor);


  p {
    cursor: pointer;
    transition: 0.3s;

    &:hover {
      color: var(--primaryRegularColor);
    }
  }

  span {
    color: var(--primaryRegularColor) !important;
    margin-right: 5px;
  }
}

.header__contacts__regular {
  width: 360px;
  padding: 10px 0;
  font-size: 13px;
}

.header__contacts__social span {
  opacity: 0.7;
  cursor: pointer;
  transition: 0.3s;
  width: 20px;

  &:hover {
    opacity: 1;
    font-size: 16px;
  }
}


// header__menu styles

.header__menu {
  display: flex;
  justify-content: space-between;
  margin-top: -25px;
  align-items: center;
  font-family: 'Work Sans', sans-serif;
  position: relative;

  img {
    margin-top: 34px;
    width: 230px;
    cursor: pointer;
  }

  span {
    font-size: 18px;
    color: black;
    cursor: pointer;
    transition: 0.2s;

  }

  span:hover {
    color: var(--primaryRegularColor) !important;
    border-color: var(--primaryRegularColor) !important;
  }
}

.header__menu__list__wrapper {
  display: flex;
  align-items: center;
}

.header__menu__list {
  display: flex;
  align-items: center;

  ul {
    border-bottom: none;
  }

  li, a, p {
    color: var(--blackTextColor);
    border-bottom: none !important;

    &:hover {
      color: var(--primaryRegularColor) !important;
    }

    p span {
      font-size: 10px !important;
      transform: rotate(90deg);
    }
  }

  .ant-menu-item-selected a {
    color: var(--primaryRegularColor)
  }
}

.header__menu__search {
  position: absolute;
  width: 300px;
  right: 0;
  top: 75px;
  box-shadow: none;
}

// header__mobile-menu styles

.header__mobile-menu__icon {
  display: none;
  margin-left: 10px;
}

.header__mobile-menu {
  font-family: 'Nanum Gothic', sans-serif;
  color: var(--blackTextColor);
  font-weight: 700;
  transition: 0.2s;
  position: sticky;
  top: 61px !important;
  background-color: white;
  width: 100%;
  box-shadow: 0 5px 5px hsla(0, 0%, 0%, 0.1);
  z-index: 2;

  a, p {
    padding: 10px 16px;
    cursor: pointer;
    color: var(--blackTextColor);
    display: block;

    &:hover {
      background-color: #f2f2f2;
      color: var(--primaryRegularColor);
    }
  }

  .ant-collapse-header {
    padding: 10px 16px !important;

    &:hover {
      background-color: #f2f2f2;
      color: var(--primaryRegularColor);
    }

    span {
      right: 10px !important;
      left: auto !important;
    }
  }
}

@media screen and (max-width: 1150px) {
  .header__menu__list {
    display: none;
  }

  .header__mobile-menu__icon {
    display: block;
  }
}

