.home__global {
  max-width: 1190px;
  padding: 0 10px;
  margin: 0 auto;
}

.home__global__title__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.home__global__title {
  span {
    color: var(--primaryRegularColor);
  }

  h2 {
    max-width: 525px;
  }
}

.home__global__title__info {
  max-width: 60%;
  margin-top: 40px;
}

.home__global__mission {
  display: flex;
  justify-content: flex-end;
  margin: 40px 0;
  background-size: 85% !important;
  //background: url("../../../../img/global_bg.jpg") 40%;
  background-position-x: 40%;
  background-repeat: no-repeat;
  //background-size: cover;
}

.home__global__mission-block {
  //background: url("../../../../img/global_bg.jpg") 40%;
  background-position-y: 80% !important;

  display: flex;
  justify-content: flex-end;
  margin: 40px 0;
  background-size: 80% !important;
  //background: url("../../../../img/global_bg.jpg") 40%;
  //background-size: cover;

}

.home__global__card {
  width: 30%;
  padding: 30px 40px;
  background-color: var(--primaryRegularColor);
  color: white;
  height: max-content;
  box-shadow: 0 20px 20px -20px var(--primaryRegularColor);

  span {
    font-size: 40px;
  }

  h3 {
    color: white;
    font-size: 30px;
    font-weight: 500;
    font-family: Montserrat, sans-serif;
    margin: 30px 0 20px;
  }

  p {
    font-size: 15px;
    line-height: 25px;
  }
}

.home__global__vision {
  display: flex;
  background-color: var(--primaryRegularColor);
  color: white;
  padding: 30px;
  box-shadow: 0 20px 20px -20px var(--primaryRegularColor);
  h3 {
    color: white !important;
  }
}

.home__global__vision__info {
  width: 70%;
  padding-left: 60px;
}

.home__global__vision__info__item {
  &:first-child {
    border-bottom: 1px solid #f2f2f2;
    margin-bottom: 20px;
    padding-bottom: 30px;
  }

  h3 {
    font-size: 20px;
    font-weight: 700;
    line-height: 49px;
  }

  p {
    margin-top: 10px;
    font-size: 15px;
    line-height: 24px;
  }
}

@media screen and (max-width: 800px) {
  .home__global__title__wrapper {
    flex-direction: column;
    align-items: flex-start;
  }

  .home__global__title__wrapper a {
    margin-top: 20px;
  }

  .home__global__title__info {
    max-width: 100%;
  }

  .home__global__mission, .home__global__vision {
    flex-direction: column;

    img {
      width: 100%;
    }
  }

  .home__global__card {
    width: 100%;
  }

  .home__global__vision__info {
    width: 100%;
    padding-left: 0;
    margin-top: 30px;
  }
}

@media screen and (max-width: 500px) {
  .home__global__title__wrapper {
    align-items: center;
    text-align: center;
  }

  .home__global__title__info {
    text-align: center;
  }
}
