.home__license {
  max-width: 1190px;
  margin: 40px auto;
  padding: 60px 10px;
  //margin: 160px auto;
  //padding: 160px 10px;
  //border-bottom: 1px solid hsla(0, 0%, 95%, 1);
  //border-top: 1px solid hsla(0, 0%, 95%, 1);
  display: flex;
}

.home__license__info {
  padding-top: 20px;
  margin-left: 50px;

  span {
    color: var(--primaryRegularColor);
  }

  h2 {
    font-size: 42px;
    font-weight: 700;
    line-height: 68px;
  }

  p {
    font-size: 25px;
    line-height: 48px;
    opacity: 0.8;
  }
}

@media screen and (max-width: 800px) {
  .home__license {
    flex-direction: column;
    padding: 60px 10px;
    margin-bottom: 60px;
  }

  .home__license__info {
    margin-left: 0;
  }
}


@media screen and (max-width: 500px) {
  .home__license__info {
    text-align: center;

    h2 {
      font-size: 40px;
    }

    p {
      font-size: 16px;
      line-height: 2;
    }
  }
}




