.home__comments {
  background-image: url("../../../../img/comments_bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 693px;
  position: relative;
  margin-top: 160px;
}

.home__comments__content {
  position: absolute;
  background-color: white;
  right: 58%;
  height: 693px;
  width: 500px;
}

.home__comments__content__title {
  position: absolute;
  top: 100px;
  left: 65px;
  margin-bottom: 80px;

  span {
    color: var(--primaryRegularColor);
  }

  h2 {
    white-space: nowrap;
  }
}

.home__comments__content_carousel {
  position: absolute;
  background-color: var(--primaryRegularColor);
  top: 270px;
  left: 65px;
  padding: 20px;
  height: max-content;
  width: 540px;
  box-shadow: 0 20px 20px -20px var(--primaryRegularColor);

  img {
    width: 15px;
    height: 15px;
    color: white;
    margin-right: 10px;
  }
}

.home__comments__content_carousel__item {
  display: flex !important;
  color: white;
}

.home__comments__content_carousel__name {
  position: absolute;
  top: 430px;
  left: 85px;
  font-family: Montserrat, sans-serif;
  font-size: 18px;
  font-weight: 500;
}

.home__comments__content_carousel__controls {
  position: absolute;
  width: 110px;
  display: flex;
  justify-content: space-between;
  bottom: 100px;
  right: -100px;
  border-radius: 30px;

  button {
    //background-color: white;
    border-radius: 30px;
    border: none;
    width: 50px;
    height: 50px;
    cursor: pointer;
    transition: 0.3s;
    background-color: hsla(var(--primaryColorValue), 69%, 62%, 1);
    color: white;
  }

  button:first-child {
    opacity: 0.5;
    box-shadow: 0 0 20px 0 rgba(50, 50, 50, 0.1);

    &:hover {
      opacity: 1;
    }
  }
}

@media screen and (max-width: 1220px) {
  .home__comments__content {
    left: 0;
    right: auto;
    width: 445px;
  }

  .home__comments__content__title,
  .home__comments__content_carousel {
    left: 10px;
  }

  .home__comments__content_carousel__name {
    left: 30px;
  }
}

@media screen and (max-width: 600px) {
  .home__comments {
    margin-top: 60px;
  }

  .home__comments__content {
    width: 100%;
  }

  .home__comments__content__title h2 {
    white-space: normal;
  }

  .home__comments__content_carousel {
    width: 100%;
    left: 0;
  }

  .home__comments__content_carousel__name {
    top: 450px;
  }

  .home__comments__content_carousel__controls {
    left: 10px;
    right: auto;
    bottom: 155px;

    button {
      box-shadow: 0 0 20px 0 rgba(50, 50, 50, 0.1);
    }

    button:first-child {
      opacity: 1;
    }
  }
}

@media screen and (max-width: 499px) {
  .home__comments__content_carousel {
    width: 100%;
    left: 0;
    top: 300px;
  }

  .home__comments__content_carousel__name {
    top: 270px;
  }

  .home__comments__content_carousel__controls {
    left: 10px;
    right: auto;
    bottom: 65px;
  }
}
