.footer__bottom__info__doc {
  display: flex;
  width: 100%;
  justify-content: center;
  margin-left: 200px;
}

.footer__bottom__info__copy {
  margin-right: 20px;
}
.footer__wrapper {
  padding: 0 40px;
  color: var(--blackTextColor);
  background-color: rgba(0, 0, 0, 0);
  font-size: 12px;
  font-weight: 400;
  line-height: 24px;
  transition: 0.2s;

  a {
    color: var(--blackTextColor);
    margin-bottom: 10px;
    display: flex;

    &:hover {
      color: var(--primaryRegularColor);
    }

    span {
      font-size: 9px;
      display: block;
      margin-right: 5px;
    }
  }
}

.footer__top {
  //border: 1px solid #f2f2f2;
  //box-shadow: 0 20px 20px -20px rgba(0, 0, 0, 0.10);
  padding: 0 70px;
  display: flex;
  justify-content: space-between;
}

.footer__top__item {
  max-width: 280px;
  padding-right: 30px;
  display: flex;
  flex-direction: column;

  &:last-child {
    padding-right: 0;
  }

  h2 {
    font-weight: 700;
    font-size: 16px;
    margin-bottom: 26px;

    &:after {
      content: "";
      display: block;
      height: 3px;
      width: 100px;
      background-color: var(--primaryRegularColor);
      margin-top: 12px;
    }
  }

  img {
    margin-top: 25px;
    width: 100%
  }

  span {
    box-shadow: none;

    &:hover {
      border-color: var(--primaryRegularColor);
    }
  }
}

.footer__bottom {
  margin-top: 40px;

  a, span {
    color: var(--primaryRegularColor);
    display: inline-block;
  }
}

.footer__bottom__info {
  display: flex;
  justify-content: space-between;
}

.footer__bottom__info__social {
  margin-right: 40px;
  margin-top: -10px !important;
  display: flex;
  margin-bottom: 20px;

  img {
    width: 50px;
    height: 35px;
    background-color: var(--primaryRegularColor);
    margin-left: 13px;
    &:last-child {
      width: 30px;
      height: 35px;
      background-color: transparent;
    }
  }

  span {
    font-size: 35px !important;
    margin-left: 10px;
    cursor: pointer;
    transition: 0.3s;

    &:hover {
      color: var(--primaryRegularColorLight);
    }
  }
}

.footer-logo-text {
  margin-top: 3px;
  line-height: 13px;
  text-align: right;
}

.footer__bottom__info__copyright {
  text-align: center;
  margin-top: 30px;
}

@media screen and (max-width: 1000px) {
  .footer__top {
    flex-direction: column;
  }

  .footer__top__item {
    margin-bottom: 30px;
    padding-right: 0;
  }
}

@media screen and (max-width: 370px) {
  .footer__bottom__info {
    flex-direction: column;
  }

  .footer__bottom__info__doc, .footer__bottom__info__social {
    text-align: center;

  }
}

