.site-block__wrapper {
  max-width: 1170px;
  padding: 0 10px;
  margin: 0 auto;
}

.franchise_img {
  height: 30%;
  width: 30%;
  margin: 0 20px 0 0;
}

.about-us {
  padding: 80px 0 160px;
}

.about-us__title h1 {
  max-width: 650px;
}

.article-after-head {
  margin-top: 0 !important;
}

.about-us__slogans {
  margin: 40px auto 0;
  max-width: 1020px;
  padding: 0 10px;
}

.about-us__slogans__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;

  &:first-child {
    margin-bottom: 80px;
  }

  h3 {
    width: 45%;
    text-align: center;
    color: hsla(0, 0%, 20%, 1);
    font-size: 30px;
    font-weight: 400;
    line-height: 58px;
  }

  p {
    font-size: 15px;
    line-height: 24px;
    width: 55%;
  }
}

.about-us__statistic__wrapper {
  background-image: url("../../../src/img/about-bg.jpg");
  background-size: cover;
  padding: 100px 0;
  color: white;
  margin-top: 160px;
}

.about-us__statistic {
  display: flex;
  justify-content: space-between;
}

.about-us__statistic__title {
  padding-left: 10px;

  span {
    color: var(--primaryRegularColor);
  }

  h2 {
    color: white;
    font-size: 50px;
    font-weight: 700;
    line-height: 58px;
  }
}

.about-us__statistic__content {
  max-width: 615px;
  padding: 0 10px 0 40px;
}

.about-us__statistic__content__description {
  font-size: 15px;
  font-weight: 400;
  line-height: 24px;
  padding-bottom: 40px;
  margin-bottom: 40px;
  border-bottom: 1px solid white;

  p {
    opacity: 0.5;
  }
}

.about-us__statistic__content__numbers {
  display: flex;
  justify-content: space-between;

  h3 {
    color: white;
    font-size: 35px;
    line-height: 58px;
    font-weight: 600;
    padding-bottom: 20px;
    font-family: 'Nanum Gothic', sans-serif;

    &:after {
      content: "";
      display: block;
      height: 3px;
      width: 70px;
      background-color: var(--primaryRegularColor);
      margin-top: 12px;
    }
  }
}

.about-us__members {
  margin-top: 160px;
}

.about-us__members__title span {
  color: var(--primaryRegularColor);
}

.about-us__members__cards {
  display: flex;
  justify-content: space-between;
  margin-top: 80px;
}

.about-us__members__cards__item {
  border-bottom-style: none;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.1) 0 20px 50px -20px;
  font-size: 15px;
  line-height: 24px;
  width: 360px;
  text-align: center;
  padding-bottom: 20px;
  transition: 0.3s;

  h3:hover {
    color: var(--primaryRegularColor);
    cursor: pointer;
    transition: 0.3s;
  }

  span {
    color: var(--primaryRegularColor);
    font-size: 13px;
    font-weight: 500;
    letter-spacing: 3px;
    line-height: 24px;

    &:last-child {
      color: #555;
      font-size: 22px;
      cursor: pointer;
      transition: 0.3s;

      &:hover {
        color: var(--primaryRegularColor);
      }
    }
  }

  h3 {
    font-size: 21px;
    font-weight: 600;
    margin: 10px 0;
  }
}

.about-us__members__description {
  display: flex;
  justify-content: space-between;
  margin-top: 80px;
}


.about-us__members__description__item {
  width: 45%;
  padding: 0 10px;
  font-size: 15px;
  line-height: 24px;
}

@media screen and (max-width: 770px) {
  .about-us__slogans__item {
    flex-direction: column;
    text-align: center;

    &:first-child {
      margin-bottom: 40px;
    }

    p {
      width: 100%;
    }
  }

  .about-us__statistic, .about-us__members__cards {
    flex-direction: column;
  }

  .about-us__statistic__content {
    padding-left: 10px;
  }

  .about-us__statistic__content__description {
    margin-top: 20px;
  }

  .about-us__members__cards__item {
    width: 100%;
    margin-bottom: 40px;
  }

  .about-us__members__description {
    flex-direction: column;
  }

  .about-us__members__description__item {
    width: 100%;

    &:first-child {
      margin-bottom: 80px;
    }
  }
}

@media screen and (max-width: 500px) {
  .franchise_img {
    width: 90%;
    height: 90%;
    margin: 0 auto 40px;
  }
  .about-us {
    padding: 80px 0;
  }

  .about-us__statistic__content__numbers {
    flex-direction: column;
  }

  .about-us__members {
    margin-top: 80px;
  }
}

.diflex {
  display: flex;
  flex-direction: row !important;
  justify-content: center !important;
  align-items:  center !important;
}

.diflex-columns {
  display: flex;
  margin-left: 20px;
  flex-direction: column !important;
  justify-content: center !important;
  align-items:  flex-start !important;
  h3 {
    padding-top: 30px;
  }
}

.blog-readmore {
  margin-top: 10px;
  cursor: pointer;
  color: var(--primaryRegularColor)

}

.blog-h3 {
  padding-top: 2px !important;
}