.main-wrapper__barrier {
  padding-bottom: 107px;
}

.margin-top-0 {
  margin-top: 0 !important;
}
.flex-column {
  display: flex;
  flex-direction: column;
}
.main-wrapper__toTop-btn {
  background-color: var(--primaryRegularColor);
  position: fixed;
  border-radius: 50px;
  right: 15px;
  bottom: 15px;
  color: white;
  cursor: pointer;
  font-size: 18px;
  font-weight: 400;
  line-height: 40px;
  text-align: center;
  width: 40px;
  z-index: 2;
  border: none;
  box-shadow: 0 0 10px white;
  transition: 0.3s;

  &:hover {
    box-shadow: 0 0 16px white;
    font-size: 20px;
  }
}

.mainTitle {
  font-size: 50px;
  font-weight: 700;
  line-height: 62px;

  &:after {
    content: '';
    display: block;
    width: 100px;
    height: 3px;
    background-color: var(--primaryRegularColor);
    margin-top: 10px;
  }
}

.primaryBtn {
  cursor: pointer;
  border-radius: 30px;
  background-color: var(--primaryRegularColor);
  color: white;
  line-height: 17px;
  font-weight: 400;
  font-size: 14px;
  padding: 15px 25px;
  width: max-content;
  height: max-content;
  transition: 0.3s;
  white-space: nowrap;
  display: block;

  &:hover {
    box-shadow: 0 20px 20px -20px var(--primaryRegularColor) !important;
    color: white;
    background-color: var(--primaryRegularColorLight);
  }

  p {
    color: white !important;
  }
}

.ghost {
  background-color: white;
  color: var(--primaryRegularColor);
  border: 1px solid var(--primaryRegularColor);

  &:hover {
    p {
      color: white !important;
    }
  }

  p {
    color: var(--primaryRegularColor) !important;
  }
}

.ant-menu-submenu-title {
  .anticon-right {
    display: none !important;
  }
}

@media screen and (max-width: 550px) {
  .main-wrapper__barrier {
    padding-bottom: 113px;
  }
}

@media screen and (max-width: 500px) {
  .leftimg {
    float: none !important;
    width: 100% !important;
    height: 200px !important;
  }
  .flex {
    flex-direction: column;
  }
  .mainTitle {
    font-size: 40px;
    text-align: center;

    &:after {
      margin: 10px auto 0;
      width: 200px;
    }
  }
}

.flex {
  display: flex;
}

