.country-cards__wrapper {
  display: flex;
  max-width: 1170px;
  padding: 0 10px;
  margin: 0 auto;
}

.country-cards__item__wrapper {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  .more-btn {
    margin-top: 20px;
    margin-left: 20px;
  }
}

.country-cards__item {
  margin: 75px 10px 10px;
  box-shadow: 0 20px 20px -20px rgba(0, 0, 0, 0.10);
  //padding: 75px 20px 75px;
  width: 220px;
  height: 220px;
  background-color: rgba(91, 167, 225, 0.1);
  border-radius: 419px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  animation: cubic-bezier;
  align-items: center;
  position: relative;

  //&:after {
  //  content: '';
  //  display: block;
  //  position: absolute;
  //  height: 100px;
  //  width: 3px;
  //  background-color: gray;
  //  right: 30px;
  //  top: 60px;
  //  opacity: 0.1;
  //}

  p {
    color: var(--primaryRegularColor);
    font-size: 15px;
    font-weight: 500;
    height: 18px;
    line-height: 18px;
  }

  h3 {
    font-family: Montserrat, sans-serif;
    font-weight: 400;
    font-size: 24px;
    line-height: 48px;
  }

  a {
    margin-top: 40px;
  }
}

@media screen and (max-width: 1100px) {
  .country-cards__wrapper {
    flex-wrap: wrap;
  }

  .country-cards__item__wrapper {
    flex-wrap: wrap;
    justify-content: space-around;
  }

  .country-cards__item {
    width: max-content;
    min-width: 350px;
  }
}

@media screen and (max-width: 800px) {

  .country-cards__wrapper {
    flex-direction: column;
  }

  .country-cards__item {
    width: 240px !important;
    min-width: 240px !important;
  }
}

.county_block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 2px solid white ;
  margin: 5px 0;
  border-radius: 5px;
  padding: 5px 0;
  h4 {
    color: white;
    display: flex;
    font-size: 20px;
  }
  p {
    display: flex;
    margin-top: 3px;
    align-items: center;
  }
  .county_btn {
    cursor: pointer;
  }
}

.county_flaq {
  width: 40px;
  height: 40px;
}
